    WaterworksDispatchCurveData() {
      this.$axios
        .post("/api/WaterworksDispatchCurveData", {
          headers: { "Content-Type": "application/json;" },
        })
        .then((res) => {
          console.log("获取调度对比曲线 WaterworksDispatchCurveData ", res);
