            OptimalMax_PumpNoArray.push(ele.PumpNo); 
            let PumpData = [];
            let PumpDataNull = [];
            let Time = [];
            ele.PumpData.map((el) => {
              PumpData.push(Number(el.Data) < 0 ? "" : el.Data);
              PumpDataNull.push(Number(el.Data) < 0 ? el.Data : "");
              Time.push(el.Time);
            });
