//调度数据---流量,压力,水泵处理后数据
          let OptimalMaxData = {
            OptimalMax_FlowData,
            OptimalMax_PressureData,
            OptimalMax_PumpCurveData,
            OptimalMax_Time,
          };
          Bus.$emit("WaterworksDispatchCurveData_OptimalMaxData_To", OptimalMaxData);
            /* ************调度数据处理结束******************** */
        })
        .catch((error) => {});
    },
     WaterworksDispatchCurveData_BF() {
