          res.data.ActualDispatchData.PressureData.map((ele) => {
            Actual_PressureData.push(ele.Data);
          });
          //当前数据---水泵
          res.data.ActualDispatchData.PumpCurveData.map((ele) => {
            Actual_PumpNoArray.push(ele.PumpNo);
            let PumpData = [];
