            Actual_PressureData,
            Actual_PumpCurveData,
            Actual_Time,
          };
          Bus.$emit("WaterworksDispatchCurveData_Actual_BF_To", ActualData);
            /* ************当前数据处理结束******************** */
              /* ************调度数据处理开始******************** */
          //调度数据
          let OptimalMax_Time = [];
          let OptimalMax_FlowData = [];
          let OptimalMax_PressureData = [];
          let OptimalMax_PumpCurveData = [];
          let OptimalMax_PumpNoArray = [];
          //调度数据---流量
          res.data.OptimalDispatchData.FlowData.map((ele) => {
            OptimalMax_Time.push(ele.Time);
            OptimalMax_FlowData.push(ele.Data);
          });
          //调度数据---压力
          res.data.OptimalDispatchData.PressureData.map((ele) => {
            OptimalMax_PressureData.push(ele.Data);
          });
          //调度数据---水泵
          res.data.OptimalDispatchData.PumpCurveData.map((ele) => {
            OptimalMax_PumpNoArray.push(ele.PumpNo); 
            let PumpData = [];
            let PumpDataNull = [];
            let Time = [];
            ele.PumpData.map((el) => {
              PumpData.push(Number(el.Data) < 0 ? "" : el.Data);
              PumpDataNull.push(Number(el.Data) < 0 ? el.Data : "");
              Time.push(el.Time);
            });
            OptimalMax_PumpCurveData.push({
              PumpData,
              Time,
              PumpNo: ele.PumpNo,
              PumpDataNull,
            });
          });
          console.log("OptimalMax_PumpCurveData---备份", OptimalMax_PumpCurveData);
//调度数据---流量,压力,水泵处理后数据
          let OptimalMaxData = {
            OptimalMax_FlowData,
            OptimalMax_PressureData,
            OptimalMax_PumpCurveData,
